import { useCallback, useMemo, useState } from "react"
import { useNavigate, useParams } from "react-router"
import * as URLS from "@rp/react/web/router"
import { PaneWidths } from "../filesPage.types"
import { getFilesPaneWidths } from "../utils/getFilesPaneWidths"

type UseFilesPagePaneInfoState = PaneWidths & {
  closeChatPane: () => void
  closeDocumentPane: () => void
  expandDocumentPane: () => void
  openChatPane: () => void
  unexpandDocumentPane: () => void
}

function useFilesPagePaneInfo(): UseFilesPagePaneInfoState {
  const { id: documentId } = useParams()
  const isDocumentPaneOpen = !!documentId

  const [isDocumentPaneExpanded, setIsDocumentPaneExpanded] = useState(false)
  const [isChatPaneOpen, setIsChatPaneOpen] = useState(true)

  const navigate = useNavigate()
  const paneWidths = useMemo(
    () => getFilesPaneWidths({ isChatPaneOpen, isDocumentPaneExpanded, isDocumentPaneOpen }),
    [isChatPaneOpen, isDocumentPaneExpanded, isDocumentPaneOpen],
  )

  const closeDocumentPane = useCallback(
    () => navigate(URLS.FILES_ROUTE, { replace: true }),
    [navigate],
  )
  const closeChatPane = useCallback(() => setIsChatPaneOpen(false), [])
  const openChatPane = useCallback(() => setIsChatPaneOpen(true), [])
  const expandDocumentPane = useCallback(() => setIsDocumentPaneExpanded(true), [])
  const unexpandDocumentPane = useCallback(() => setIsDocumentPaneExpanded(false), [])

  return {
    ...paneWidths,
    closeChatPane,
    closeDocumentPane,
    expandDocumentPane,
    openChatPane,
    unexpandDocumentPane,
  }
}

export { useFilesPagePaneInfo }
export type { UseFilesPagePaneInfoState }
